<template>
    <div class="shopbag">
        <!-- 购物袋数据 -->
        <div class="box" v-if="shopbagData.length > 0">
            <van-swipe-cell v-for="(item , index) in shopbagData" :key="index" v-model="result">
                <van-card
                    :price="item.price"
                    :desc="item.rule"
                    :title="item.name"
                    :thumb="item.large_img"
                >
                    <template #bottom>
                        <van-stepper
                            theme="round"
                            button-size="22"
                            disable-input
                            v-model="item.count"
                            @change="modifyCount(item)"
                        />
                        <van-checkbox
                            v-model="item.isCheck"
                            :name="item"
                            checked-color="#0C34BA"
                            @change="simpleSelect"
                        ></van-checkbox>
                    </template>
                </van-card>
                <template #right>
                    <van-button
                        square
                        text="删除"
                        type="danger"
                        class="delete-button"
                        @click="removeOneShopbag(item.sid , index)"
                    />
                </template>
            </van-swipe-cell>
        </div>
        <!-- 如果购物袋为空显示的界面 -->
        <div v-else>
            <van-empty description="购物袋空空如也,去逛一逛!">
                <van-button round color="#0C34BA" class="bottom-button" @click="goClassify">去逛一逛</van-button>
            </van-empty>
        </div>
        <div class="bottom-box">
            <van-submit-bar
                :price="total"
                button-text="提交订单"
                button-color="#0C34BA"
                @submit="commit"
            >
                <van-checkbox v-model="allCheck" @click="allSelect" checked-color="#0C34BA">全选</van-checkbox>
            </van-submit-bar>
        </div>
    </div>
</template>

<script>
import "../../assets/less/shopbag.less";
export default {
    data() {
        return {
            value: 1,
            isEdit: false,
            allCheck: false,
            shopbagData: [],
            total: 0,
            result: [],
            total: 0,
            show: false,
        };
    },
    created() {
        this.findShopbag();
    },
    methods: {
        // 跳转至分类
        goClassify() {
            this.$router.push({ name: "Classify" });
        },
        // 获取购物袋数据
        findShopbag() {
            let tokenString = localStorage.getItem("CSTK");
            if (!tokenString) {
                return;
            }

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
            });

            this.axios({
                method: "GET",
                url: "/findAllShopcart",
                params: {
                    appkey: this.appkey,
                    tokenString,
                },
            })
                .then((result) => {
                    this.$toast.clear();
                     

                    if (result.data.code == 5000) {
                        result.data.result.map((v) => {
                            v.isCheck = false;
                        });

                        this.shopbagData = result.data.result;
                    } else {
                        this.$toast(result.data.msg);
                    }
                })
                .catch((err) => {
                    this.$toast.clear();
                     
                });
        },
        // 全选商品
        allSelect() {
            this.shopbagData.map((v) => {
                v.isCheck = this.allCheck;
            });

            this.sum();
        },
        // 单选商品
        simpleSelect() {
            this.sum();
             
            for (let i = 0; i < this.shopbagData.length; i++) {
                if (!this.shopbagData[i].isCheck) {
                    this.allCheck = false;
                    return;
                }
            }

            this.allCheck = true;
             
        },
        // 更改商品数量
        modifyCount(item) {
            let tokenString = localStorage.getItem("CSTK");
            if (!tokenString) {
                return;
            }
            let data = this.queryString({
                appkey: this.appkey,
                tokenString,
                sid: item.sid,
                count: item.count,
            });

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
            });

            this.axios({
                method: "POST",
                url: "/modifyShopcartCount",
                data,
            })
                .then((result) => {
                    this.$toast.clear();
                     
                    this.sum();
                    if (result.data.code !== 6000) {
                        this.$toast(result.data.msg);
                    }
                })
                .catch((err) => {
                    this.$toast.clear();
                     
                });
        },
        // 删除单个商品
        removeOneShopbag(sid, index) {
            this.removeShopbag([sid])
                .then((result) => {
                    this.$toast.clear();
                     
                    if (result.data.code == 7000) {
                        this.shopbagData.splice(index, 1);
                    }

                    this.$toast(result.data.msg);

                    this.sum();
                })
                .catch((err) => {
                    this.$toast.clear();
                     
                });
        },
        // 删除商品
        removeShopbag(sids) {
            let tokenString = localStorage.getItem("CSTK");
            if (!tokenString) {
                return;
            }

            let data = this.queryString({
                appkey: this.appkey,
                tokenString,
                sids: JSON.stringify(sids),
            });

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
            });

            return this.axios({
                method: "POST",
                url: "/deleteShopcart",
                data,
            });
        },
        // 计算总价
        sum() {
            this.total = 0;
            this.shopbagData.map((v) => {
                if (v.isCheck) {
                    this.total += v.count * v.price;
                }
            });

            this.total *= 100;
        },
        // 序列化参数
        queryString(o) {
            let params = "";
            for (let key in o) {
                params += `${key}=${o[key]}&`;
            }

            return params.slice(0, -1);
        },
        // 提交订单信息
        commit() {
            //查找需要购买的商品, sid
            let sids = [];
            this.shopbagData.map((v) => {
                if (v.isCheck) {
                    sids.push(v.sid);
                }
            });
            if (sids.length === 0) {
                this.$toast("请选择购买商品");
                return;
            }

            sids = sids.join("-");

            this.$router.push({ name: "Pay", query: { sids } });

        },
    },
};
</script>

<style lang="less" scoped>
</style>